@charset "UTF-8";
html {
  font-size: 10px; }

body {
  background: #262161;
  background: #efefef; }
  body:before {
    content: '';
    display: block;
    height: 10px;
    width: 100%; }

.inner {
  max-width: 900px;
  margin: 0 auto; }
  @media all and (max-width: 900px) {
    .inner {
      padding: 2rem; } }

.nav {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: space-between;
  background: #38507B; }
  .nav__section {
    display: flex; }
    .nav__section--search {
      flex: 1 1 auto; }
  .nav__item {
    display: flex; }
  .nav__link {
    background: #38507B;
    color: white;
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    text-transform: uppercase;
    padding: 1.2rem 2rem 1rem 2rem;
    display: block;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: transform 0.2s;
    border-bottom: 5px solid transparent; }
    .nav__link svg {
      width: 40px;
      transition: all 0.2s;
      fill: white;
      margin-bottom: 1rem; }
    .nav__link:hover, .nav__link--active {
      border-bottom-color: rgba(0, 0, 0, 0.2);
      border-right-color: rgba(0, 0, 0, 0.05);
      background: #F2D796 85% fixed; }
      .nav__link:hover svg, .nav__link--active svg {
        transform: scale(1.2); }
    .nav__link--logo:hover svg {
      transform: none; }
    .nav__link--logo svg {
      width: 200px;
      margin: 0; }

@media all and (max-width: 1200px) {
  .nav__link {
    padding-left: 1rem;
    padding-right: 1rem; }
    .nav__link svg {
      width: 30px; }
    .nav__link--logo svg {
      width: 100px; } }

@media all and (max-width: 900px) {
  .nav {
    flex-wrap: wrap; }
    .nav__section {
      order: 1; }
      .nav__section--search {
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        order: 2;
        width: 100%; } }

@media all and (max-width: 650px) {
  .nav__section {
    width: 100%; }
    .nav__section--user {
      order: -1;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1); }
      .nav__section--user .avatar {
        min-width: 20px;
        height: 20px; }
      .nav__section--user svg {
        display: none; }
  .nav__item {
    width: 33.333333%; }
  .nav__link {
    width: 100%; }
    .nav__link svg {
      max-width: 100%; }
    .nav__link--logo svg {
      width: 100%; }
  .heart-count:before {
    content: '♥️';
    margin-right: 1rem; } }

.search {
  position: relative;
  width: 100%;
  display: flex; }
  .search__results {
    background: white;
    position: absolute;
    width: 100%;
    top: 100%;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    z-index: 5; }
  .search__input {
    font-size: 30px;
    background: none;
    color: white;
    outline: 0;
    border: 0; }
  .search__result {
    padding: 10px;
    display: block;
    border-bottom: 1px solid #ececec; }
    .search__result--active {
      background: #f1f1f1; }

.names {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.name {
  background: white;
  margin: 10px 0;
  width: 30%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1), 0 5px 10px rgba(0, 0, 0, 0.05); }
  .name--wide {
    width: 100%; }
  .name__details {
    padding: 2rem; }
    .name__details p {
      line-height: 1.2;
      margin-bottom: 0; }
  @media all and (max-width: 850px) {
    .name {
      width: 48%; } }
  @media all and (max-width: 550px) {
    .name {
      width: 100%; } }

.name__actions {
  position: relative;
  z-index: 2;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2), 0 1px 0 rgba(255, 255, 255, 0.2);
  margin-bottom: 50px;
  padding-top: 10px;
  padding-bottom: 20px;
  display: flex;
  color: white;
  align-items: center;
  justify-content: space-around; }

.name__action {
  font-size: 10px; }
  .name__action svg {
    width: 25px;
    fill: white; }
  .name__action--edit a {
    border-bottom: 0; }

.name__hero {
  position: relative;
  padding: 0 10px 0 10px;
  text-align: right; }
  .name__hero:before {
    display: block;
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    background: linear-gradient(45deg, #05b3d6 0%, rgba(5, 179, 214, 0) 70%), linear-gradient(135deg, #0958ec 10%, rgba(9, 88, 236, 0) 80%), linear-gradient(225deg, #d209ec 10%, rgba(210, 9, 236, 0) 80%), linear-gradient(315deg, #05e129 100%, rgba(5, 225, 41, 0) 70%);
    opacity: 0.6;
    z-index: 1;
    clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%); }
  .name__hero img {
    height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    position: absolute;
    left: 0;
    object-fit: cover;
    clip-path: polygon(0 0, 100% 0, 100% 90%, 0% 100%); }

.hide {
  text-indent: -999999px;
  font-size: 0; }

.button {
  border: 0;
  background: #F2D796;
  color: #303030;
  font-family: 'Panama';
  font-weight: 600; }

.avatar {
  min-width: 50px;
  height: 50px;
  border-radius: 50%;
  align-self: center; }

.card {
  background: white;
  padding: 5rem;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.15); }

pre.error {
  padding: 5rem;
  background: white;
  line-height: 3;
  white-space: pre-line; }

@font-face {
  font-family: 'panama';
  src: url("/fonts/panama-bold-webfont.woff2") format("woff2"), url("/fonts/panama-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

html {
  font-size: 10px; }

body {
  font-size: 1.5rem;
  color: #303030; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 5rem;
  font-family: 'Panama';
  font-weight: 600;
  letter-spacing: -1px; }

a {
  text-decoration: none;
  color: #303030; }

p a {
  border-bottom: 2px solid #F2D796; }

p {
  line-height: 2;
  font-size: 1.6rem; }

.title {
  word-wrap: break-word;
  position: relative;
  z-index: 2;
  margin: 0;
  font-size: 40px;
  z-index: 2;
  line-height: 1.1;
  transform: skew(0, -3deg);
  line-height: 1.1; }
  .title--long {
    font-size: 30px; }
  .title:before {
    content: '';
    width: 50px;
    left: 0;
    top: 0;
    height: 100%;
    display: block;
    position: absolute;
    z-index: -1;
    transform: skew(-5deg); }
  .title a {
    border-bottom: 0;
    background-image: linear-gradient(to right, rgba(242, 215, 150, 0.8) 100%, rgba(242, 215, 150, 0.8) 50%); }

.title--single {
  max-width: 600px;
  margin-top: -9rem;
  font-size: 10rem;
  text-align: center; }



